import Pill from "@/components/common/PIll";
import { HEROSECTION_BG_IMAGE, PROPERTYLISTINGSAMPLE } from "@/constants";
import { useState } from "react";

export default function Home() {
  const [activeFilter, setActiveFilter] = useState("All");

  const allCategories = [
    "All",
    ...Array.from(
      new Set(
        PROPERTYLISTINGSAMPLE.flatMap(property => property.category)
      )
    )
  ];

  return (
    <main className="w-full space-y-8 font-source-sans">
      <section className="p-4 text-center">
        <h2 className="text-2xl font-bold">Welcome to Our Platform</h2>
        <p className="text-gray-600">
          Discover the best destinations and deals tailored for you.
        </p>
      </section>

      <section 
        className="relative flex items-center justify-center w-full h-full p-4"
        style={{ 
          backgroundImage: `url(${HEROSECTION_BG_IMAGE})`, 
          width: '100%', 
          minHeight: '280px', 
          backgroundSize: 'cover', 
          borderRadius: '27px', 
          backgroundPosition: 'center' 
        }}
      >
        <div className="relative z-10 flex flex-col items-center justify-center w-full h-full max-w-2xl gap-5">
          <h1 className="font-bold text-center text-white text-7xl">
            Find your favorite place here!
          </h1>
          <p className="px-4 text-2xl font-semibold text-center text-white">
            The best prices for over 2 million properties worldwide.
          </p>
        </div>
      </section>

      {/* Filter Section */}
      <section className="px-4 py-6">
        <div className="container mx-auto">
          <h3 className="mb-4 text-xl font-semibold">Filter Properties</h3>
          <div className="flex flex-wrap gap-3">
            {allCategories.map((category) => (
              <Pill
                key={category}
                label={category}
                active={activeFilter === category}
                onClick={() => setActiveFilter(category)}
              />
            ))}
          </div>
        </div>
      </section>

      <div className="p-4 text-center bg-yellow-300">
        <h3 className="text-xl font-semibold">Explore Top Destinations</h3>
        <p className="text-black">
          Browse through our curated list of popular travel spots and hidden
          gems.
        </p>
      </div>
    </main>
  );
}